import React, { useEffect } from "react";
import { FormCheck, Table } from "react-bootstrap";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { ReactSVG } from "react-svg";
import addNoteIcon from "../../icons/plus-circle.svg";
import { getTableRecords } from "../../services/crmDataService";
import NotesModal from "../notesModal";
import "./table.css";

const DynamicTable = ({ zohoId }) => {
  const [tableData, setTableData] = React.useState([]);
  const [show, setShow] = React.useState(false);
  const [selectedRow, setSelectedRow] = React.useState(null);

  useEffect(() => {
    async function intiTable() {
      if (zohoId) {
        await getTableRecords(zohoId).then((data) => {
          console.log(JSON.parse(data?.details.output).table_data);
          let transTableData = JSON.parse(data?.details.output).table_data;
          transTableData.forEach((row, index) => {
            row.completeDate = false;
            row.notes = [];
            if(row.last_note) {
              row.notes.push(row.last_note);
            }
            row.note = "";
            if(row.deadline) {
              let dateArray = row?.deadline.split("-");
              row.deadline =  new Date(dateArray[0], dateArray[1] - 1, dateArray[2]);  
          }
          });
          setTableData(transTableData);
        });
      }
    }
    intiTable();
  }, [zohoId]);



  function handleModalVisibility(id) {
    setShow(true);
    //find the object in the array and set it to selectedRow
    setSelectedRow(
      tableData.find((item) => {
        // console.log("the item is :", item);
        return item.id === id;
      })
    );
  }

  function handleDeadline(date, index) {
    //find the row on the base of index and update the deadline
    const updatedTableData = tableData.map((row, i) => {
      if (i === index) {
        row.deadline = date;
      }
      return row;
    });
    setTableData(updatedTableData);
  }

  function handleNotes(value) {
    const updatedTableData = tableData.map((row) => {
      if (row.id === selectedRow.id) { 
        row.notes.push(value);
        row.note = "";
      }
      return row;
    });
    setTableData(updatedTableData);
  }

  return (
    <>
      <Table striped bordered hover className="m-5 p-5 table-style">
        <thead>
          <tr>
            <th>Complete Date</th>
            <th>Complete Group</th>
            <th>Key Date</th>
            <th>Deadline</th>
            <th>Last Note</th>
            <th>Add Note</th>
          </tr>
        </thead>
        <tbody>
          {tableData?.map((item, index) => (
            <tr key={index}>
              <td>
                <FormCheck
                  type="checkbox"
                  checked={item.completeDate}
                  onChange={() => {
                    const updatedTableData = tableData.map((row, i) => {
                      if (i === index) {
                        row.completeDate = !row.completeDate;
                      }
                      return row;
                    });
                    setTableData(updatedTableData);
                  }}
                />
              </td>
              <td>{item.complete_group}</td>
              <td>{item.key_date}</td>
              <td>
                <DatePicker
                  selected={item.deadline}
                  onChange={(date) => handleDeadline(date, index)}
                  dateFormat="MM/dd/yyyy"
                />
              </td>
              <td>{item.notes[item.notes.length -1 ]}</td>
              <td>
                {/* <img
                  src={addNoteIcon}
                  onClick={() => handleModalVisibility(item.id)}
                ></img> */}
                <ReactSVG
                  src={addNoteIcon}
                  onClick={() => handleModalVisibility(item.id)}
                />
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      <NotesModal
        show={show}
        handleClose={() => setShow(false)}
        handleSave={handleNotes}
        note={selectedRow ? selectedRow.note : "No note found in selected row"}
      />
    </>
  );
};

export default DynamicTable;
