import React, { useEffect } from "react";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { ReactSVG } from "react-svg";
import editDateIcon from "../../icons/edit-date.svg";
import emailIcon from "../../icons/envelope.svg";
import { invokeDummyFunction } from "../../services/crmDataService";
import { ZOHO } from "../../vendor/ZSDK";
import "./header.css";
const CustomInput = React.forwardRef((props, ref) => {
  return (
    <div>
      <label onClick={props.onClick} ref={ref}>
        {props.value || props.placeholder}
      </label>
    </div>
  );
});

const Header = ({
  title,
  setZohoID,
  closingDate = null,
}) => {
  const [ClosingDate, setClosingDate] = React.useState(new Date());
  const [showDatePicker, setShowDatePicker] = React.useState(false);
  const [headerDetails, setHeaderDetails] = React.useState({});

  useEffect(() => {
    async function initializeSDK() {
      let details;

      ZOHO.embeddedApp.on("PageLoad", async function (data) {
        ZOHO.CRM.UI.Resize({ width: "1000", height: "720" });
        details = await invokeDummyFunction(data.EntityId[0]);
        setZohoID(data.EntityId[0]);
       setHeaderDetails(JSON.parse(details?.details?.output));
        
      });

      ZOHO.embeddedApp.init().then(() => {
      });


    }

    initializeSDK();
  }, []);

  useEffect(() => {

    if(headerDetails){
        let dateArray = headerDetails?.closing_date?.split("-");
        let date;
        if (dateArray) {
          date = new Date(dateArray[0], dateArray[1] - 1, dateArray[2]);
          setClosingDate(new Date(date));
        } 

        
    }
  }, [headerDetails]);


  return (
    <>
      <Container fluid>
        <Row>
          <Col>
            <img
              src={require("./CHR.png")}
              alt="CHR Logo"
              width="150"
              height="40"
              className="logo"
            />
            <h4 className="title">{title}</h4>

            {closingDate ? (
              <span
                className="align-self-baseline d-block"
                onClick={() => setShowDatePicker(!showDatePicker)}
              >
                <h6 className="title">
                  Contract: {headerDetails.contract_yr} | Closing Date:{"  "}
                  <DatePicker
                  wrapperClassName="date-picker-wrapper"
                    selected={ClosingDate}
                    onChange={(date) => setClosingDate(date)}
                    dateFormat="MM/dd/yyyy"
                    onCalendarClose={() => setShowDatePicker(false)}
                    customInput={<CustomInput />}
                  />{""} 
                   {/* <img src={require('../../icons/edit-date.svg').default} style={{margin : "8px"}} width="10" height="10"></img> */}
                   <ReactSVG src={editDateIcon} />
                </h6>{""}
              </span>
            ) : (
              <h6 className="title">Contract: {headerDetails.contract_yr}</h6>
            )}
          </Col>
          <Col>
            <h6 className="right-side">{headerDetails.closing_date}</h6>
            <h6 className="right-side">Transaction Manager:</h6>
            <h6 className="right-side text-decoration-underline">
              {" "}
              <a href="mailto:abc@gmail.com">
                {headerDetails.tm_name} <img src={emailIcon} width="10" height="10" />
              </a>
            </h6>
            <h6 className="right-side">{headerDetails.tm_phone}</h6>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Header;
