import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

import React, { useEffect, useState } from 'react';

function NotesModal({ show, handleClose, handleSave, note }) {

    const [notes, setNotes] = useState(note);

    useEffect(() => {
        setNotes(note);
    }, [note]);

    function handleChange(event) {
        setNotes(event.target.value);
    }

    function handleSaveNotes() {
        handleSave(notes);
        setNotes('');
        handleClose()
    }

    function handleDismiss() {
        setNotes(note);
        handleClose()
    }
    
  return (
    <>

      <Modal 
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      show={show} onHide={handleDismiss}>
        <Modal.Header closeButton>
          <Modal.Title>Add Notes</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <textarea value={notes} onChange={handleChange} className="form-control" rows="5" id="comment"></textarea>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleDismiss}>
            Close
          </Button>
          <Button variant="primary" onClick={handleSaveNotes}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default NotesModal;